#root, html, body {
  width: 100%;
  height: 100%;
}

.app {
  margin: auto;
  width: 500px;
  height: 100%;
  display: flex;
  flex-direction: column;
  border-left: solid 3px gold;
  border-right: solid 3px gold;
  padding: 0px 40px;
}

header {
  background-color: white;
  padding: 0px 20px;
}

.header-bar {
  display: flex;
  align-items: center;
}


article {
  padding: 0px 20px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
} 

footer {
  min-height: 40px;
  text-align: center;
}

.player-holder {
  padding: 20px 10px;
  height: 100%;
  display: flex;
}

.player-holder > div {
  flex-grow: 1;
}

select {
  padding: 5px 10px;
  font-size: 1.1em;
}

@media (max-width: 700px) {
  .app {
    width: 100%;
    border: none;
    padding: 0px;
  }
  .header-bar {
    flex-direction: column;
  }
  .header-bar > div:nth-child(2){
    margin-top: 20px;
    width: 80%;
  }
}