.switch-body {
    display: flex;
    flex-direction: row;
    border: solid 2px gold;
    background-color: gold;
    border-radius: 15px;
}

.switch-btn {
    flex-grow: 1;
    padding: 8px 5px;
    text-align: center;
    font-weight: bold;
    border-radius: 15px;
    cursor: pointer;
}

.switch-btn-active {
    border: solid 2px white;
    background-color: white;
}