.note {
    font-size: 10em;
}

.next-box {
    display: flex;
    flex-direction: column;
    margin: 20px 30px;
}

.next {
    font-size: 2em;
    margin-left: 15px;
}

.note-holder {
    display: flex;
    justify-content: center;
    align-items: center;
}

.big-btn {
    background: none;
    border: solid 8px black;
    border-radius: 50%;
    width: 250px;
    height: 250px;
    font-size: 6em;
}

.small-btn {
    background: none;
    border: solid 3px black;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin: 5px;
}

.btn:hover {
    background-color: lightgray;
    cursor: pointer;
}

@media (max-width: 700px) {
    .big-btn {
        width: 150px;
        height: 150px;
    }
}